<template>
    <!-- 过渡动画 -->
    <transition name="fade">
        <div class="img-view" @click="bigImg">
            <!-- 遮罩层 -->
            <div class="img-layer"></div>
            <div class="img">
                <img class ="bigImgClass" src="../../static/img/1.jpeg">
            </div>
        </div>
    </transition>
</template>
<script>
export default {
  props: ['imgSrc'],
  methods: {
    bigImg () {
    // 发送事件
      this.$emit('clickit')
    }
  }
}
</script>
<style scoped>
/*动画*/
.fade-enter-active,
.fade-leave-active {
    transition: all .3s linear;
    //transform: translate3D(0, 0, 0);
}

.fade-enter,
.fade-leave-to {
    transform: translateX(50%);
    //transform: translate3D(50%, 0%, 0);
}

/* bigimg */

.img-view {
    position: relative;
    width: 100%;
    height: 100%;
}

/*遮罩层样式*/
.img-view .img-layer {
    position: fixed;
    z-index: 999;
    top: 0;
    left: 0;
    background: rgba(0, 0, 0, 0.7);
    width: 100%;
    height: 100%;
    overflow: hidden;
}
.bigImgClass {
    border-radius: 40px;
}

.img-view .img img {
    //max-width: 100%;
    /*通过max-width和max-height限制图片大小*/
    max-width:980px;
    max-height:530px;
    display: block;
    position: absolute;
    left: 0;
    right: 0;
    margin: auto;
    z-index: 1000;
}
</style>



